<template>
  <div class="container">
    <div class="loader-container">
      <div class="la-line-scale-pulse-out-rapid la-2x">
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapMutations } from 'vuex'
import { setToken } from 'utils/tool'

export default {
  mounted () {
    // 路由判断主动进入
    let {newToken} = this.$route.query
    if (newToken) {
      setToken(newToken)
      this.setToken(newToken)
      this.$router.replace({name: 'Home'})
    } else {
      this.$router.replace({name: 'Error'})
    }
  },
  methods: {
    ...mapMutations({
      setToken: 'SET_TOKEN'
    })
  }
}
</script>
<style lang='stylus' scoped>
  .container
    width 100vw
    height 100vh
    display flex
    align-items center
    justify-content center
</style>



